/* Habits Page Specific Styles */

/* Habits Container */
.habits-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  min-height: 100vh;
  position: relative;
}

/* Background Decorations */
.habits-container::before {
  content: '';
  position: absolute;
  top: 10%;
  left: 5%;
  font-size: 8rem;
  opacity: 0.05;
  animation: float 8s ease-in-out infinite;
  z-index: 0;
}

.habits-container::after {
  content: '';
  position: absolute;
  top: 60%;
  right: 5%;
  font-size: 6rem;
  opacity: 0.05;
  animation: float 6s ease-in-out infinite reverse;
  z-index: 0;
}

@keyframes float {
  0%, 100% { transform: translateY(0px) rotate(0deg); }
  50% { transform: translateY(-20px) rotate(5deg); }
}

/* Habits Header */
.habits-header {
  text-align: center;
  margin-top: 4rem;
  margin-bottom: 3rem;
  position: relative;
  z-index: 1;
}

.habits-title {
  font-size: 3rem;
  font-weight: 700;
  background: linear-gradient(135deg, #ff6b6b 0%, #4ecdc4 50%, #96ceb4 100%);
  background-size: 200% 100%;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  animation: titleGlow 3s ease-in-out infinite alternate;
}

@keyframes titleGlow {
  0% { filter: drop-shadow(0 0 10px rgba(255, 107, 107, 0.3)); }
  100% { filter: drop-shadow(0 0 20px rgba(78, 205, 196, 0.5)); }
}

.habits-title .emoji {
  font-size: 3.5rem;
  animation: bounce 2s ease-in-out infinite;
}

.habits-subtitle {
  font-size: 1.3rem;
  color: #666;
  margin: 0;
  font-weight: 300;
  letter-spacing: 1px;
}

/* Message Styles */
.message {
  padding: 1rem 1.5rem;
  border-radius: 15px;
  margin: 1rem 0;
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 500;
  position: relative;
  z-index: 1;
  animation: slideIn 0.5s ease-out;
}

@keyframes slideIn {
  from { transform: translateY(-20px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.message.success {
  background: linear-gradient(135deg, #d4edda 0%, #c3e6cb 100%);
  color: #155724;
  border: 1px solid #c3e6cb;
}

.message.error {
  background: linear-gradient(135deg, #f8d7da 0%, #f5c6cb 100%);
  color: #721c24;
  border: 1px solid #f5c6cb;
}

/* Habits Actions */
.habits-actions {
  display: flex;
  justify-content: center;
  margin: 2rem 0;
  position: relative;
  z-index: 1;
}

.add-habit-btn {
  font-size: 1.2rem;
  padding: 1rem 2.5rem;
  border-radius: 30px;
  background: linear-gradient(135deg, var(--primary-color, #ff6b6b) 0%, var(--secondary-color, #ee5a52) 100%);
  color: white;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
  gap: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.add-habit-btn:hover {
  transform: translateY(-5px) scale(1.05);
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.3);
  background: linear-gradient(135deg, var(--secondary-color, #ff5252) 0%, var(--primary-color, #e53935) 100%);
}

.add-habit-btn:active {
  transform: translateY(-2px) scale(1.02);
}

/* Habit Form Container */
.habit-form-container {
  background: white;
  border-radius: 25px;
  padding: 2.5rem;
  box-shadow: 0 15px 35px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.8);
  margin: 2rem 0;
  position: relative;
  z-index: 1;
  animation: formSlideIn 0.6s ease-out;
}

@keyframes formSlideIn {
  from { transform: translateY(30px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.habit-form-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: linear-gradient(90deg, #ff6b6b, #4ecdc4, #96ceb4, #ffeaa7);
  background-size: 200% 100%;
  animation: gradientShift 4s ease-in-out infinite;
  border-radius: 25px 25px 0 0;
}

@keyframes gradientShift {
  0%, 100% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
}

/* Form Styles */
.habit-form {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.form-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 2rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-label {
  font-weight: 600;
  color: #333;
  font-size: 0.95rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.form-label::after {
  content: '*';
  color: #ff6b6b;
  font-size: 1.2rem;
  display: none; /* Hide for optional fields */
}

.form-input {
  padding: 1rem;
  border: 2px solid #e1e1e1;
  border-radius: 12px;
  font-size: 1rem;
  transition: all 0.3s ease;
  background: #f9f9f9;
  color: #333;
}

.form-input:focus {
  outline: none;
  border-color: #ff6b6b;
  background: white;
  box-shadow: 0 0 0 4px rgba(255, 107, 107, 0.1);
  transform: translateY(-2px);
}

.form-input:hover {
  border-color: #4ecdc4;
}

textarea.form-input {
  resize: vertical;
  min-height: 100px;
  font-family: inherit;
}

/* Form Actions */
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #f0f0f0;
}

.btn {
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 25px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  text-decoration: none;
  font-size: 1rem;
}

.btn-secondary {
  background: linear-gradient(135deg, #6c757d 0%, #5a6268 100%);
  color: white;
}

.btn-secondary:hover {
  background: linear-gradient(135deg, #5a6268 0%, #495057 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(108, 117, 125, 0.3);
}

.btn-primary {
  background: linear-gradient(135deg, var(--primary-color, #ff6b6b) 0%, var(--secondary-color, #ee5a52) 100%);
  color: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.btn-primary:hover {
  background: linear-gradient(135deg, var(--secondary-color, #ff5252) 0%, var(--primary-color, #e53935) 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

/* Habits Grid */
.habits-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(380px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
  position: relative;
  z-index: 1;
}

.habit-card {
  background: white;
  border-radius: 20px;
  padding: 2rem;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.8);
  transition: all 0.3s ease;
  position: relative;
  overflow: hidden;
  animation: cardFadeIn 0.6s ease-out;
}

/* Delete button positioned horizontally next to frequency */
.delete-btn-horizontal {
  background: rgba(231, 76, 60, 0.9);
  color: white;
  border: none;
  border-radius: 8px;
  padding: 0.4rem 0.8rem;
  font-size: 0.75rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  opacity: 0.8;
  white-space: nowrap;
}

.delete-btn-horizontal:hover {
  background: rgba(231, 76, 60, 1);
  opacity: 1;
  transform: scale(1.05);
}

@keyframes cardFadeIn {
  from { transform: translateY(30px); opacity: 0; }
  to { transform: translateY(0); opacity: 1; }
}

.habit-card:hover {
  transform: translateY(-8px) scale(1.02);
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.15);
}

.habit-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 5px;
  background: linear-gradient(90deg, #ff6b6b, #4ecdc4, #96ceb4);
  background-size: 200% 100%;
  animation: gradientShift 3s ease-in-out infinite;
}

/* Habit Header */
.habit-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
}

.habit-frequency {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.5rem 1rem;
  background: rgba(78, 205, 196, 0.1);
  border-radius: 20px;
  border: 1px solid rgba(78, 205, 196, 0.2);
}

.frequency-emoji {
  font-size: 1.5rem;
}

.frequency-name {
  font-size: 0.9rem;
  font-weight: 600;
  color: #4ecdc4;
  text-transform: capitalize;
}

.habit-status {
  font-size: 1.2rem;
}

/* Habit Content */
.habit-content {
  margin-bottom: 1.5rem;
}

.habit-name {
  font-size: 1.4rem;
  font-weight: 700;
  color: #333;
  margin-bottom: 0.75rem;
  line-height: 1.3;
}

.habit-description {
  color: #666;
  font-size: 0.95rem;
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

/* Habit Stats */
.habit-stats {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1.5rem 0;
  padding: 1rem;
  background: #f8f9fa;
  border-radius: 15px;
  border: 1px solid #e9ecef;
}

.streak-info {
  flex: 1;
}

.current-streak,
.longest-streak {
  margin-bottom: 0.5rem;
}

.streak-label {
  font-size: 0.8rem;
  color: #666;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.streak-value {
  font-size: 1.2rem;
  font-weight: 700;
  display: block;
  margin-top: 0.25rem;
}

.check-ins {
  text-align: center;
  padding-left: 1rem;
  border-left: 1px solid #e1e1e1;
}

.check-ins-label {
  font-size: 0.8rem;
  color: #666;
  font-weight: 500;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.check-ins-value {
  font-size: 1.2rem;
  font-weight: 700;
  color: #333;
  display: block;
  margin-top: 0.25rem;
}

/* Habit Actions */
.habit-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding-top: 1rem;
  border-top: 1px solid #f0f0f0;
}

.checkin-btn {
  flex: 1;
  padding: 1rem 2rem;
  border-radius: 15px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 1rem;
  font-weight: 600;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.checkin-btn:not(.checked-in) {
  background: linear-gradient(135deg, var(--primary-color, #ff6b6b) 0%, var(--secondary-color, #ee5a52) 100%);
  color: white;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
}

.checkin-btn:not(.checked-in):hover {
  background: linear-gradient(135deg, var(--secondary-color, #ff5252) 0%, var(--primary-color, #e53935) 100%);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.3);
}

.checkin-btn.checked-in {
  background: linear-gradient(135deg, #27ae60 0%, #229954 100%);
  color: white;
  cursor: not-allowed;
  opacity: 0.8;
}

.btn-danger {
  background: linear-gradient(135deg, #ff7675 0%, #d63031 100%);
  color: white;
  padding: 0.75rem 1rem;
  border-radius: 15px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  font-size: 0.9rem;
  font-weight: 600;
}

.btn-danger:hover {
  background: linear-gradient(135deg, #ff5252 0%, #c0392b 100%);
  transform: translateY(-2px);
  box-shadow: 0 5px 15px rgba(255, 118, 117, 0.4);
}

/* No Habits State */
.no-habits {
  grid-column: 1 / -1;
  text-align: center;
  padding: 4rem 2rem;
  background: white;
  border-radius: 25px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.8);
  position: relative;
  z-index: 1;
}

.no-habits-icon {
  font-size: 5rem;
  margin-bottom: 1.5rem;
  opacity: 0.6;
  animation: pulse 2s ease-in-out infinite;
}

@keyframes pulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.1); }
}

.no-habits h3 {
  font-size: 1.8rem;
  color: #333;
  margin-bottom: 1rem;
  font-weight: 600;
}

.no-habits p {
  color: #666;
  font-size: 1.1rem;
  line-height: 1.6;
}

/* Loading State */
.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 60vh;
  position: relative;
  z-index: 1;
}

.loading-spinner {
  text-align: center;
  color: #333;
}

.loading-spinner .heart {
  font-size: 4rem;
  animation: heartbeat 1.5s ease-in-out infinite;
}

@keyframes heartbeat {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.2); }
}

.loading-spinner p {
  margin-top: 1rem;
  font-size: 1.2rem;
  font-weight: 500;
  color: #666;
}

/* Responsive Design */
@media (max-width: 768px) {
  .habits-container {
    padding: 1rem;
  }

  .habits-title {
    font-size: 2.2rem;
    flex-direction: column;
    gap: 0.5rem;
  }

  .habits-title .emoji {
    font-size: 2.5rem;
  }

  .habits-subtitle {
    font-size: 1.1rem;
  }

  .habits-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .form-row {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .form-actions {
    flex-direction: column;
    gap: 0.75rem;
  }

  .habit-actions {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
  }

  .habit-card {
    padding: 1.5rem;
  }

  .habit-form-container {
    padding: 1.5rem;
  }

  .delete-btn-horizontal {
    padding: 0.3rem 0.6rem;
    font-size: 0.7rem;
  }

  .habits-container::before,
  .habits-container::after {
    font-size: 4rem;
  }
}

@media (max-width: 480px) {
  .habits-title {
    font-size: 1.8rem;
  }

  .habits-subtitle {
    font-size: 1rem;
  }

  .habit-card {
    padding: 1rem;
  }

  .habit-form-container {
    padding: 1rem;
  }

  .btn {
    padding: 0.6rem 1.2rem;
    font-size: 0.9rem;
  }

  .add-habit-btn {
    font-size: 1rem;
    padding: 0.8rem 2rem;
  }
}
